<template>
  <div style="text-align:center;">
    <h3 class="mdl-color-text--primary">{{client.name}}</h3>
    <client-form
      ref="form"
      :client="client"
    >
    </client-form>
    <button
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
      @click="updateClient">
      Update
    </button>
    <div>
      <bank-list-view
        :holder="client"
        :type="'client'"
      ></bank-list-view>
    </div>
  </div>
</template>
<script>
import { ClientForm } from '.'
import { mapActions } from 'vuex'
import { MESSAGES } from '@/constants'
import { BankListView } from 'BANKS_APP/components'
import { reInitiateForm } from '@/helpers.js'
export default {
  props: {
    client: {
      type: Object,
      required: true
    }
  },
  components: {
    ClientForm,
    BankListView
  },
  updated () {
    reInitiateForm()
  },
  computed: {
    form: {
      get () {
        return this.$refs.form
      }
    },
    banks () {
      return this.client.banks || []
    }
  },
  methods: {
    ...mapActions([
      'addClient'
    ]),
    updateClient () {
      this.form.valid().then(valid => {
        if (valid) {
          this.addClient(this.form.clientParams).then(() => {
            this.$toastr.s(MESSAGES.success_add)
            this.$router.push({ name: 'clients.list' })
          }).catch(e => {
            this.$toastr.e(e)
          })
        }
      })
    }
  }
}
</script>



